const mobile = window.matchMedia('(max-width: 1050px)');
window.addEventListener('resize', () => {
  if(mobile.matches) {
    mobileNavHelp = true;
    document.getElementById("grayOverlay").style.display = "none";
    document.getElementById("nav-bar").style.display = "none";
  } else {
    mobileNavHelp = false;
    if(document.getElementById("grayOverlay") !== null) {
      document.getElementById("grayOverlay").style.display = "none";
      document.getElementById("nav-bar").style.display = "flex";
    }
  }
});

class ContentSection extends Component {
  render() {
    switch(this.props.content) {
      case "About":
        return <About></About>;
      case "Experience":
        return <Experience></Experience>;
      case "Skills":
        return <Skills></Skills>;
      case "Resume":
        return <Resume></Resume>;
      case "Projects":
        return <Projects></Projects>;
      case "Contact":
        return <Contact></Contact>;
      default:
        return "No file found";
    }
  }
}

class InfoSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.id,
      title: this.props.title,
      headerPic: this.props.headerPic
    };
  }

  render() {
    return(
      <div id={this.state.id} className='flex-col section-info'>
        <div className='info-title' style={{backgroundImage: `url(${this.state.headerPic})`}}>
          <p>{this.state.title}</p>
        </div>
        <ContentSection content={this.state.title}></ContentSection>
      </div>
    );
  }
}

class Info extends Component {
  render() {
    return(
      <div id='info' className='flex-col'>
        {sectionInfo.map(function(info) {
          return <InfoSection key={sectionInfo.indexOf(info)} id={info.title.toLowerCase()} title={info.title} headerPic={info.image}></InfoSection>
        })}
      </div>
    );
  }
}

class NavTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ref: '#' + this.props.title.toLowerCase(),
      title: this.props.title,
      className: 'nav-tab hvr-underline-from-left-' + this.props.color
    };
  }
  
  render() {
    let clickPlace = this.state.ref;
    return(
      <div className={this.state.className} onClick={function() {
        if(mobileNavHelp === true) {
          mobileNavHelp = false;
          document.getElementById("nav-bar").style.display = "none";
          document.getElementById("grayOverlay").style.display = "none";
        }
        let differnece = Math.abs(document.getElementById(clickPlace.substring(1)).getBoundingClientRect().top / 5);
        jump(document.querySelector(clickPlace), {
          duration: 300 + differnece,
          easing: easeInOutQuad
        });
      }}>
        <section className="nav-hover-text">{this.state.title}</section>
      </div>
    );
  }
}

class NavBar extends Component {
  render() {
    return(
      <div id='nav-bar' className='flex-col'>
        {sectionInfo.map(function(info) {
          return <NavTab key={sectionInfo.indexOf(info)} title={info.title} color={info.color}></NavTab>
        })}
        <hr />
        <div id='nav-name' className='flex-col' onClick={function() {
          if(mobileNavHelp === true) {
            mobileNavHelp = false;
            document.getElementById("nav-bar").style.display = "none";
            document.getElementById("grayOverlay").style.display = "none";
          }
          let differnece = Math.abs(document.getElementById('about').getBoundingClientRect().top / 5);
          jump(document.querySelector('#about'), {
            duration: 300 + differnece,
            easing: easeInOutQuad
          });
        }}>
          <p id='nav-title'>Jordon Bowden</p>
          <p id='nav-desc'>Full Stack Developer</p>
        </div>
      </div>
    );
  }
}

class App extends Component {
  render() {
    return(
      <div id='site-hold' className='flex-row'>
        <div className="hamburger" onClick={function() {
          var x = document.getElementById("nav-bar");
          var y = document.getElementById("grayOverlay");
          if (x.style.display === "flex" && y.style.display === "flex") {
            mobileNavHelp = false;
            x.style.display = "none";
            y.style.display = "none";
          } else {
            mobileNavHelp = true;
            x.style.display = "flex";
            y.style.display = "flex";
          }
        }}>
          {/* <button>hep</button> For mobile testing */}
          <i className="fa fa-bars"></i>
        </div>
        <div id="grayOverlay" onClick={function() {
          document.getElementById("nav-bar").style.display = "none";
          document.getElementById("grayOverlay").style.display = "none";
        }}></div>
        <NavBar></NavBar>
        <Info></Info>
      </div>
    );
  }
}

export default App;