import React, { Component } from 'react';

class Contact extends Component {
  render() {
    return(
      <div id="content-holder" className="flex-col">
        <span className="content-large-section">
          <span className="flex-row contact-row">
            <i className="contact-icon fas fa-envelope-square"></i>
            <h2>jordon.bowden@gmail.com</h2>
          </span>
        </span>
        <a href="https://linkedin.com/in/jordonbowden/" className="content-large-section" target="_blank" rel="noopener noreferrer">
          <span className="flex-row contact-row">
            <i className="contact-icon fab fa-linkedin"></i>
            <h2>https://linkedin.com/in/jordonbowden/</h2>
            <i className="contact-icon-small fas fa-external-link-alt"></i>
          </span>
        </a>
        {/* Probably figure out a form to email me here */}
      </div>
    );
  }
}

export default Contact;